.nav-tabs {
    &.nav-tabs--research {
        display: flex;
        align-items: stretch;

        li {
            flex: 1;

            button {
                margin: 0;
                width: 100%;
                height: 100%;
                text-align: center;
                font-size: 0.875em;
                background-color: $grey;
                padding-top: 30px;
                padding-bottom: 30px;
                color: $dark;
                border-radius: 0;
                border: none;
                position: relative;

                &:after {
                    content: '';
                    background-color: $red;
                    height: 6px;
                    position: absolute;
                    bottom: 0;
                    width: 100%;
                    left: 0;
                    right: 0;
                    display: none;
                }

                &.active {
                    font-weight: bold;
                    color: $dark;
                    background-color: $grey;

                    &:after {
                        display: block;
                    }
                }
            }
        }
    }
}